<template>
  <section class="content" id="content">
    <article class="markdown-section" id="main">
      <div v-html="content"></div>
    </article>
    <div id="gitalk-container"></div>
  </section>
</template>

<script>
import 'gitalk/dist/gitalk.css'
import Gitalk from 'gitalk'
export default {
  name: 'HelloWorld',
  props: {
    title: String,
  },
  data(){
    return {
      input: '',
    }
  },
  watch : {
    '$route'()
    {
      this.getPost ()
    }
  },
  methods: {
    getPost: function () {
        this.apiSetting.getPost.url = this.$route.path + '.md';
        this.request(this.apiSetting.getPost).then(
            res => {
                this.input = res.data
            },
            error => {
                console.log(error)
            })
    },
    addClass: function (elements,cName) {
        elements.className += " " + cName;
    },
    gitalk: function () {
      const gitalk = new Gitalk({
        clientID: '90800c440ac8bdf5cd79',
        clientSecret: 'db434ac0c7de8d42cd69a1facc85cedb2ffb6bd1',
        repo: 'donghaichen.github.io',
        owner: 'donghaichen',
        admin: ['donghaichen'],
        // id: location.pathname,      // Ensure uniqueness and length less than 50
        id: 'test',      // Ensure uniqueness and length less than 50
        distractionFreeMode: false  // Facebook-like distraction free mode
      })

      gitalk.render('gitalk-container')
    }

  },
  mounted: function ()
  {
    this.getPost ()
    this.gitalk ()
  },
  computed: {
    content: function () {
      return this.marked(this.input, { sanitize: true })
    }
  }

}
</script>

